* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
}
.container:before {
  content: "";
  position: absolute;
  height: 1500px;
  width: 1500px;
  top: -10%;
  right: 48%;
  transform: translateY(-49%);
  background-image: linear-gradient(-45deg, #02a79f 0%, #1b5b7e 100%);
  transition: 1.8s ease-in-out;
  border-radius: 50%;
}

.container .signin {
  /* position: absolute;
  top: 10px;
  right: 15%;
  transform: translate(50px, 50px);
  z-index: 3; */
  position: absolute;
  top: calc(50% - 220px);
  right: 15%;
  transform: translate(50px, 50px);
  z-index: 3;
  text-align: center;
}

