.social-text {
  padding: 0.7rem 0;
  font-size: 1rem;
}

.social-media {
  display: flex;
  justify-content: center;
}

.social-icon {
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 6px 5px;
  color: #4481eb;
  border: none;
  border: 1px solid rgb(180, 180, 180);
  border-radius: 50%;
  text-decoration: none;
  font-size: 20px;
  transition: 0.3s;
}
.social-icon:nth-child(2) {
  color: rgb(201, 203, 211);
}
.social-icon:nth-child(3) {
  color: rgb(64, 159, 223);
}
.social-icon:nth-child(4) {
  color: rgb(133, 126, 126);
}

.social-icon:hover {
  color: #437fe7;
}
