.button button:hover {
  background-color: #1c84e6;
}
.social-text {
  text-align: center;
}

.container .photos-container {
  position: absolute;
  z-index: 2;
  width: 100%;
}
.container .photos-container .photos {
  position: absolute !important;
  left: 20px;
  transform: translate(50px, 50px);
}
.container .photos-container .content {
  position: relative;
}
.container .photos-container .content h3 {
  color: white;
  transform: translate(100px);
}
.container .photos-container .content p {
  color: white;
  line-height: 1.4;
  margin-top: 30px;
  font-weight: 600;
}
.photos-container .my-image {
  width: 100%;
}
.photos-container .image {
  width: 50%;
  margin-top: 50px;
  filter: hue-rotate(
322deg);
}

