}
@media (max-width: 1200px) {
  .container .signin {
    right: 10%;
  }
}
@media (max-width: 960px) {
  .container .photos-container .content {
    margin-left: -15px;
  }
}

@media (max-width: 880px) {
  .container .signin {
    position: absolute;
    right: 0;
    z-index: 3;
    transform: scale(1.3);
    display: block;
    width: 100%;
    top: 45%;
  }
  .container {
    min-height: 800px;
    height: 100vh;
    /* max-width: 1300px; */
  }
  /* .form-container{
    width: 100%; 
 } */
  .container .signin {
    /* overflow: hidden; */
    width: 100%;
    top: 30%;
    right: 20px;
    transform: translate(20px, 50px);
    z-index: 3;
  }
  .photos-container {
    z-index: 10;
    left: 10%;
    top: -5%;
  }

  .container .photos-container .photos .content {
    position: relative;
    left: -25px;
  }

  .container .photos-container .content h3 {
    transform: translate(50px);
  }
  .container .photos-container .content p {
    margin-top: 30px;
    font-weight: 600;
    transform: translate(-10px);
  }
  .container .photos-container .photos {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
  .container .photos-container .my-image .image {
    width: 230px;
    margin-right: 100px;
  }
  .container:before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-40%);
    left: 20%;
    bottom: 66%;
    right: initial;
    top: initial;
  }
}
@media (max-width: 550px) {
  .container .photos-container .photos {
    margin-left: -50px;
  }

  .container .photos-container .content {
    margin-bottom: -50px;
  }
  .container .photos-container .content h3 {
    transform: translate(30px);
  }
  .container .photos-container .content p {
    margin-top: 20px;
    font-size: 12px;
    transform: translate(13px);
  }
  .container .photos-container .my-image .image {
    width: 200px;
  }
}
@media (max-width: 470px) {
  .container .photos-container .my-image .image {
    width: 150px;
  }
}
@media (max-width: 360px) {
  .container .photos-container .my-image .image {
    width: 130px;
  }
  .container .photos-container .content h3 {
    font-size: 17px;
  }
}
